<template>
  <div class="FindbookBox">
    <div class="FindbookTitle"></div>
    <div class="FindbookContent">
       <swiper :options="swiperOption">
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
        <swiper-slide  class="FindbookProduct">
          <router-link to="/vueProject/API" tag="p"></router-link>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script>
  import '@/assets/css/Book/FindBook.css'
  export default {
    name: 'findbook',
    data(){
      return {
        msg: {},
        swiperOption: {
          slidesPerView: 4,
          slidesPerColumn: 2,
          paginationClickable: true,
          spaceBetween: 10,
          breakpoints: {
            1024: {
              slidesPerView: 4,
              spaceBetween: 10
            },
            768: {
              slidesPerView: 3,
              spaceBetween: 10
            },
            640: {
              slidesPerView: 2,
              spaceBetween: 10
            },
            320: {
              slidesPerView: 2,
              spaceBetween: 10
            }
          }
        }
      }
    },
    created(){
      this.$nextTick(()=>{
        this.FindbookColor();
      });
    },
    methods: {
      FindbookColor(){
        let el = $(".FindbookProduct");
        for(let i=0;i<el.length;i++){
          let r = parseInt(Math.random() * 256);
          let g = parseInt(Math.random() * 256);
          let b = parseInt(Math.random() * 256);
          el.eq(i).css({'color':'rgb(' + r + ',' + g + ',' + b + ')','border-color':'rgb(' + r + ',' + g + ',' + b + ')'})
        }
      }
    }
  }
</script> 



