<template>
    <!-- Start -->
  <nav class="navbar navbar-default navbar-fixed-top">
      <div class="container-fluid">
          <!-- Brand and toggle get grouped for better mobile display -->
        <div class="navbar-header">
          <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
              <span class="sr-only">Toggle navigation</span>
              <span class="icon-bar"></span>
              <span class="icon-bar"></span>
              <span class="icon-bar"></span>
          </button>
            <router-link id="logo" to="/vueProject/" tag="a" class="navbar-brand"></router-link>
          </div>

          <!-- Collect the nav links, forms, and other content for toggling -->
        <div class="collapse navbar-collapse navbar-right" id="navList">
        <!-- <div class="collapse navbar-collapse navbar-right" id="bs-example-navbar-collapse-1"> -->
          <ul class="nav navbar-nav navbar-left homeBanner">
            <li><router-link class="movie navKind" exact to="/vueProject/movie" tag="a"> <span class="sr-only">(current)</span></router-link></li>
            <li><router-link class="book navKind" exact to="/vueProject/book" tag="a"></router-link></li>
            <li><router-link class="broadcast navKind" exact to="/vueProject/broadcast" tag="a"></router-link></li>
            <li><router-link class="group navKind" exact to="/vueProject/group" tag="a"></router-link></li>
          </ul> 
          <form class="navbar-form navbar-right">
            <div class="form-group">
              <input type="text" class="form-control" placeholder="Search">
            </div>
            <button type="button" class="btn btn-default"></button>
          </form>
        </div><!-- /.navbar-collapse -->
      </div><!-- /.container-fluid -->
  </nav>
    <!-- End -->
</template>

<script>
  import '@/assets/css/Home/App.css'
  export default {
    name: "banner",
    mounted(){
      this.navToggle();
    },
    methods: {
      navToggle(){
        let $navList = $('#navList');
        let $navButton = $('.navbar-header').find('button:eq(0)');
        let $logo = $('#logo');
        $logo.on('click',function(){
           $navList.slideUp();
        })
        $navButton.on('click',function(){
           $navList.slideToggle();
        })
        $navList.find('li').on("click",function(){
          $navList.slideToggle();
        });
      }
    }
  }
</script>



